.site-layout .ant-layout-header {
  background: #fff;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.06);
}
.ant-layout {
  min-height: 100%;
  background-color: #f5f5f5;
}
.ant-layout-content {
  overflow: auto;
}
.ant-layout-footer {
  background-color: transparent;
  text-align: center;
}
.ant-layout .header-link {
  padding: 5px;
  margin: 0 10px;
}
.main-container {
  max-width: 1100px;
  margin: 0 auto;
}
a {
  color: #000;
}
a:hover,
a:active {
  color: #555;
}
.page-header {
  font-size: 43px;
  text-align: center;
  margin-bottom: 0;
}
.page-subtitle {
  text-align: center;
  font-size: 14px;
}
.main-container section {
  margin-bottom: 20px;
}
.one-card {
  overflow: hidden;
  cursor: pointer;
  margin-bottom: 20px;
}
.one-card .ant-card-cover > div {
  padding-bottom: 55%;
  background-size: 105%;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 1s;
}
.one-card:hover .ant-card-cover > div {
  background-size: 130%;
}
.ant-carousel .slick-slide {
  text-align: center;
  overflow: hidden;
}
.ant-carousel .slick-dots li button {
  background-color: #aaa;
}
.ant-carousel .slick-dots li.slick-active button {
  background: #1890ff;
}
.ant-carousel .slick-slide .cover {
  width: 100%;
  padding-bottom: 55%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #fafafa;
}
.ant-drawer-body {
  padding: 0;
}
.drawer-content {
  padding: 30px;
  white-space: pre-line;
  text-align: justify;
}
.ant-drawer-content-wrapper {
  width: 640px !important;
}
@media (max-width: 640px) {
  .ant-drawer-content-wrapper {
    width: 100% !important;
  }
}
